<template>
  <div>
    <el-table
      :data="
        tableData.filter(
          (data) =>
            !search || data.name.toLowerCase().includes(search.toLowerCase())
        )
      "
      :height="300"
      style="width: 100%"
    >
      <el-table-column label="编号" width="180">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.id }}</span>
        </template>
      </el-table-column>
      <el-table-column label="姓名" width="180">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>姓名: {{ scope.row.name }}</p>
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium">{{ scope.row.name }}</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column label="性别" prop="sex"></el-table-column>
      <el-table-column label="生日" prop="bir"></el-table-column>
      <el-table-column label="地址" prop="address"></el-table-column>
      <el-table-column label="操作" align="right">
        <template slot="header">
          <el-input v-model="search" size="mini" placeholder="输入关键字搜索" />
        </template>
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
            >编辑</el-button
          >
          <el-popconfirm
            confirm-button-text="好的"
            cancel-button-text="不用了"
            icon="el-icon-info"
            icon-color="red"
            title="确定删除吗？"
            @confirm="handleDelete(scope.$index, scope.row)"
          >
            <el-button size="mini" type="danger" slot="reference"
              >删除</el-button
            >
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>

    <el-row>
      <el-col :span="6" :offset="18">
        <!-- 分页 -->
        <el-pagination
          layout="prev, pager, next,jumper,total,sizes"
          style="margin: 10px 0px"
          :page-size="size"
          :current-page="pageNow"
          :page-sizes="[2, 4, 6, 8]"
          prev-text="上一页"
          next-text="下一页"
          @current-change="findPage"
          @size-change="findSize"
          :total="total"
        >
        </el-pagination
      ></el-col>
    </el-row>

    <el-button
      type="success"
      size="mini"
      style="margin: 10px 0px"
      @click="clearForm()"
      >添加</el-button
    >
    <transition name="el-zoom-in-center">
      <div v-show="show" class="transition-box">
        <el-form
          :hide-required-asterisk="false"
          :rules="rules"
          ref="userForm"
          :model="form"
          label-width="80px"
          label-suffix=":"
        >
          <el-form-item label="姓名" prop="name">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="生日">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="form.bir"
              style="width: 100%"
              value-format="yyyy-MM-dd"
            ></el-date-picker>
          </el-form-item>

          <el-form-item label="性别">
            <el-radio-group v-model="form.sex">
              <el-radio label="男"></el-radio>
              <el-radio label="女"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="地址">
            <el-input type="textarea" v-model="form.address"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit('userForm')"
              >保存信息</el-button
            >
            <el-button @click="clearForm()">取消</el-button>
          </el-form-item>
        </el-form>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      form: {
        name: "",
        bir: "",
        sex: "男",
        address: "",
      },
      search: "",
      show: true,
      total: 0,
      pageNow: 1,
      size: 4,
      rules: {
        name: [{ required: true, message: "请输入用户姓名", trigger: "blur" }],
      },
    };
  },
  methods: {
    // 每页数
    findSize(size) {
      this.size = size;
      this.findData(this.pageNow, size);
    },
    // 分页
    findPage(page) {
      this.pageNow = page;
      this.findData(page, this.size);
    },
    clearForm() {
      this.form = { sex: "男" };
      this.show = !this.show;
    },
    handleEdit(index, row) {
      // 展示编辑表单
      this.show = true;
      // 回显信息
      this.form = row;
    },
    handleDelete(index, row) {
      this.$http
        .get("http://localhost:8989/user/delete?id=" + row.id)
        .then((res) => {
          if (res.data.status) {
            this.$message({
              message: +res.data.msg,
              type: "success",
            });
            // 刷新表单
            this.findData();
          } else {
            this.$message.error(res.data.msg);
          }
        });
    },
    onSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http
            .post("http://localhost:8989/user/saveOrUpdate", this.form)
            .then((res) => {
              if (res.data.status) {
                this.$message({
                  message: "恭喜你，" + res.data.msg,
                  type: "success",
                });
                // 清空表单信息
                this.form = { sex: "男" };
                // 隐藏表单
                this.show = false;
                // 刷新表单
                this.findData();
              } else {
                this.$message.error(res.data.msg);
              }
            });
        } else {
          this.$message.error("表单信息不合法");
          return false;
        }
      });
    },
    findData(page, size) {
      page = page ? page : this.pageNow;
      size = size ? size : this.size;
      this.$http
        .get(
          "http://localhost:8989/user/findByPage?pageNow=" +
            page +
            "&pageSize=" +
            size
        )
        .then((res) => {
          // this.tableData = res.data;
          this.tableData = res.data.users;
          this.total = res.data.total;
        });
    },
  },
  created() {
    this.findData();
  },
};
</script>

<style>
.transition-box {
  margin-bottom: 10px;
  width: 1000px;
  height: 500px;
  border-radius: 4px;
  padding: 40px 20px;
  box-sizing: border-box;
  margin-right: 20px;
}
</style>


