<template>
  <div>
    <el-carousel indicator-position="outside">
      <el-carousel-item v-for="item in Imgs" :key="item">
        <el-image
          :src="item"
          fit="fill"
          style="height:1000px"
        ></el-image>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
import homeImg1 from "../assets/IndexImgs/1.jpeg";
import homeImg2 from "../assets/IndexImgs/2.jpeg";
import homeImg3 from "../assets/IndexImgs/3.jpeg";
export default {
  name: "index",

  data() {
    return {
      Imgs: [homeImg1, homeImg2, homeImg3],
    };
  },
};
</script>

<style>
</style>


